import  axios from "axios"
import jwt_decode from "jwt-decode"
import router from '@/router';

export default {
    name: "Home",
    data() {
      return {
        username: "",
        password: "",
        baseURL: "https://backend-turtle-3gjmgjwho-tiagobonito44-hotmailcom.vercel.app/",
        awaitMsg: "",
        errorLogin: "",
        sucessLogin: "",
        
      }
    },
    mounted() {
        document.body.style.background = "#EAE6E6"
    },
    methods: {
        async getLogin() {
            this.awaitMsg = "Tentando se conectar, aguarde..";
            // dados usuario e senha
            const loginData = {
              username: this.username,
              password: this.password
            };
          
            try {
              const res = await axios.post(`${this.baseURL}login`, loginData);
              const token = JSON.stringify(res.data.token);
              const decoded = jwt_decode(token);
              const username = decoded.username;
              const email = decoded.email;
              const phone = decoded.phone;
              const adress = decoded.adress;
              const userId = decoded.id;
              const biography = decoded.biography;
          
            
              localStorage.setItem("username", username);
              localStorage.setItem("email", email);
              localStorage.setItem("adress", adress);
              localStorage.setItem("phone", phone);
              localStorage.setItem("token", token);
              localStorage.setItem("userId", userId);
             
        
          
              router.push({ name: "forum" });
            } catch (err) {
              console.log(err.response);
            }
          
            this.validations();
          },

        validations() {
          if(this.awaitMsg) {
            this.errorLogin = ""
          }

            if(!this.username || !this.password) {
                this.errorLogin = "Verifique os campos."
                this.awaitMsg = ""
            } 


            if(!this.token) {
               setTimeout(() => {
                this.errorLogin = "Inválido, tente novamente."
                this.awaitMsg = ""
               }, 3000)
            } 

            if(this.token) {
                setTimeout(() => {
                    this.errorLogin = ""
                    this.awaitMsg = ""
                   }, 3000)
            }
        }
        
    }
}